  background-image: url("../img/spritesheet.png");
  width: 9px;
  height: 13px;
  background-position: -6px -6px;
}

.contacts-phone:after {
  content: '';
  position: absolute;
  top: 4px;
  left: 0;
  background-image: url("../img/spritesheet.png");
  width: 10px;
  height: 11px;
  background-position: -58px -203px;
}

.contacts-email:after {
  content: '';
  position: absolute;
  top: 7px;
  left: 0;
  background-image: url("../img/spritesheet.png");
  width: 12px;
  height: 8px;
  background-position: -244px -183px;
}

.page-footer {
  color: #cacaca;
}

.footer-info {
  background-color: #2c2c2c;
  padding: 70px 0;
}

.footer-info-title {
  text-transform: uppercase;
  font-size: 14px;
  font-weight: 800;
  margin: 0 0 30px 0;
}

.footer-info-content {
  font-size: 14px;
  margin: 0 0 40px 0;
}

.footer-info-post {
  margin: 0 0 5px 0;
  padding: 0;
}

.footer-info-cite {
  display: block;
  margin: 0 0 25px 0;
  padding: 0;
}

.footer-copyright {
  background-color: #292929;
  padding: 35px 0;
}

@media screen and (max-width: 1064px) {
  .article-content {
    text-align: center;
  }
}

@media screen and (max-width: 992px) {
  .container {
    width: 85%;
    margin: 0 auto;
  }
  .home,
  .our-project,
  .footer-info,
  .contacts,
  .features,
  .journal,
  .portfolio-1,
  .pages {
    padding: 10% 0 5% 0;
  }
  .main-nav {
    padding: 0;
    margin: 10px 0 0 0;
    width: 70%;
    vertical-align: middle;
  }
  .main-nav li {
    padding: 0 0 0 0;
    margin: 0 1% 0 0;
  }
  .home-title {
    font-size: 30px;
  }
  .home-subtitle {
    font-size: 23px;
  }
  .portfolio-article {
    padding: 0 10px;
  }
  .portfolio-row-bottom {
    margin: 0;
  }
  .portfolio-article:before {
    display: none;
  }
  .slider-image {
    display: block;
    height: auto;
    width: 100%;
  }
  .article-image {
    height: auto;
    width: 60%;
  }
  .features-md {
    display: none;
  }
  .contacts-form input:not([type="submit"]) {
    padding: 1%;
    height: 10%;
  }
  .subject {
    height: 10px;
  }
  .contacts-form {
    margin: 0 0 10% 0;
    position: relative;
  }
  .contacts-form input[type="submit"] {
    position: absolute;
    left: 50%;
    bottom: -45px;
    transform: translate(-50%, 0);
  }
  .footer-info-title {
    margin: 0 0 2% 0;
  }
  .footer-info-content {
    margin: 0 0 3% 0;
  }
  .footer-info-content:last-child {
    margin: 0 0 10% 0;
  }
  .footer-info-div {
    margin: 0 0 10% 0;
  }
  .footer-post:last-child {
    margin: 0 0 10% 0;
  }
}

@media screen and (max-width: 768px) {
  .home,
  .our-project,
  .footer-info,
  .contacts,
  .features,
  .journal,
  .portfolio-1,
  .pages {
    padding: 20% 0 5% 0;
  }
  .header-logo {
    position: relative;
    top: 0;
    left: 50%;
    transform: translate(-50%, 0);
  }
  nav {
    display: none;
  }
  .home-title {
    font-size: 20px;
  }
  .home-subtitle {
    display: none;
  }
  .pages-line {
    width: 82%;
  }
  .portfolio-article {
    margin: 0 0 5% 0;
  }
  .portfolio-slider {
    display: none;
  }
  .portfolio-slider-xs {
    display: block;
    margin: 0 0 5% 0;
  }
  .journal-row:not(:nth-of-type(1)) {
    display: none;
  }
  .features-step-ideas:before,
  .features-step-solution:before,
  .features-step-touch:before,
  .features-step-winning:before,
  .features:before {
    display: none;
  }
  .features-step-title {
    text-align: center;
  }
  .features-step-content {
    text-align: center;
  }
  .features-step-ideas:after,
  .features-step-solution:after,
  .features-step-touch:after,
  .features-step-winning:after {
    left: 50%;
    transform: translate(-50%, 0);
  }
  .contacts-form {
    margin: 0 0 20% 0;
  }
  .footer-info-div {
    display: none;
  }
  .footer-info-div:first-child,
  .footer-info-div:last-child {
    display: block;
  }
}
