    .features-step-solution:before,
    .features-step-touch:before,
    .features-step-winning:before,
    .features:before {
        display: none; }

    .features-step-title {
        text-align: center; }

    .features-step-content {
        text-align: center; }

    .features-step-ideas:after,
    .features-step-solution:after,
    .features-step-touch:after,
    .features-step-winning:after {
        left: 50%;
        transform: translate(-50%, 0); }

    .contacts-form {
        margin: 0 0 20% 0; }

    .footer-info-div {
        display: none; }

    .footer-info-div:first-child,
    .footer-info-div:last-child {
        display: block; } }
